.button {
  appearance: none;
  cursor: pointer;

  font-size: 1rem;
  font-weight: var(--font-weight-bold);

  padding: 1em 2.5em;

  border: none;
  border-radius: 2em;

  color: var(--white);
  background-color: var(--primary);

  &:focus {
    outline: 0.125em dashed red;
  }
}
